@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.disabled {
  pointer-events: none;
}

body {
  font-family: "PT Mono", "Roboto", sans-serif;
  scroll-behavior: smooth;
}

.container {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding-left: 50px;
    padding-right: 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 14px 25px 19px;
  font-family: "Roboto Condensed", "Roboto", "Arial", sans-serif;
  font-weight: 700;
  align-self: center;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
  justify-content: center;
  letter-spacing: 0.05em;
  border-radius: 3px;
  font-size: 18px;
  line-height: 21px;
  background-color: #00b2ff;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (max-width: 1023px) {
  .btn {
    font-size: 18px;
    line-height: 21px;
  }
}
@media (max-width: 767px) {
  .btn {
    font-size: 14px;
    line-height: 16px;
    padding: 10px 30px;
  }
}
.btn--transparency {
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #f2f5f7;
  width: 160px;
  min-height: 40px;
  box-sizing: border-box;
  padding: 10px 15px;
  font-size: 14px;
  line-height: 16px;
}
.btn--black {
  background-color: #041427;
  border: none;
  font-size: 16px;
  line-height: 19px;
  padding: 15px 25px;
}
@media (max-width: 1023px) {
  .btn--black {
    font-size: 16px;
    padding: 25px;
    max-width: 265px;
    min-width: 265px;
    padding-top: 16px;
    padding-bottom: 14px;
  }
}
@media (max-width: 767px) {
  .btn--black {
    font-size: 16px;
    line-height: 19px;
    width: 100%;
    padding: 13px 25px;
  }
}
.btn--blue {
  background-color: rgba(255, 255, 255, 0);
  color: #00b2ff;
  border: 1px solid #00b2ff;
  width: 180px;
  min-height: 45px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 1023px) {
  .btn--blue {
    font-size: 14px;
    line-height: 16px;
    padding: 10px 30px;
  }
}
@media (max-width: 767px) {
  .btn--blue {
    font-size: 14px;
    line-height: 16px;
    width: 160px;
    min-height: 35px;
  }
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn:hover, .btn:focus {
    opacity: 0.8;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
.modal h2 {
  margin: 0 0 11px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 30px;
  font-weight: 400;
  color: #ffffff;
}
.modal p {
  margin: 0 0 40px 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  max-width: 380px;
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 40px 43px 85px 25px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 500px;
  min-height: 610px;
  padding: 40px 40px 40px 40px;
  background-color: #00b3ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}

.modal__close-btn {
  position: absolute;
  top: 20px;
  right: 14px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal__close-btn svg {
  width: 20px;
  height: 20px;
}

.header {
  padding: 30px 0 35px;
  background: linear-gradient(180deg, rgb(4, 24, 39) 84.9%, rgba(4, 24, 39, 0) 100%);
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
}
@media (max-width: 1023px) {
  .header {
    padding: 20px 0 25px;
    position: absolute;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 15px 0 20px;
  }
}

.header__container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .header__container {
    padding-left: 26px;
    padding-right: 35px;
  }
}
@media (max-width: 1023px) {
  .header__container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.header__contacts {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
  row-gap: 20px;
  width: 465px;
  margin-bottom: 8px;
  margin-top: 13px;
}
@media (max-width: 1023px) {
  .header__contacts {
    height: 40px;
    width: 350px;
    margin-bottom: 15px;
    margin-right: 13px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .header__contacts {
    margin-bottom: 0;
    margin-right: 0;
    margin-top: -5px;
    width: 200px;
  }
}

.header__link {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
  margin-right: 5px;
}
.header__link:hover {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .header__link {
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin-right: 0;
  }
}
@media (max-width: 1023px) {
  .header__link span {
    display: none;
  }
}
.header__link svg {
  width: 20px;
  height: 20px;
  margin-right: 15px;
}
@media (max-width: 767px) {
  .header__link svg {
    margin-right: 0;
  }
}

.header__callback {
  margin-right: 60px;
  margin-top: 5px;
  align-self: flex-start;
}
@media (max-width: 1023px) {
  .header__callback {
    display: none;
  }
}

.logo {
  width: 215px;
  height: 55px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
