.header {
  padding: 30px 0 35px;
  background: $black-peral-to-right;
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;

  @include vp-1023 {
    padding: 20px 0 25px;
    position: absolute;
  }

  @include vp-767 {
    padding: 15px 0 20px;
  }
}

.header__container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;


  @include vp-767 {
    padding-left: 26px;
    padding-right: 35px;
  }

  @include vp-1023 {
    padding-left: 50px;
    padding-right: 50px;

  }
}

.header__contacts {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
  row-gap: 20px;
  width: 465px;
  margin-bottom: 8px;
  margin-top: 13px;

  @include vp-1023 {
    height: 40px;
    width: 350px;
    margin-bottom: 15px;
    margin-right: 13px;
    margin-top: 0;
  }

  @include vp-767 {
    margin-bottom: 0;
    margin-right: 0;
    margin-top: -5px;
    width: 200px;
  }
}

.header__link {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: $color-alice-blue;
  margin-right: 5px;

  &:hover {
    opacity: 0.8;
  }

  @include vp-1023 {
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin-right: 0;
  }

  span {
    @include vp-1023 {
      display: none;
    }
  }

  svg {
    width: 20px;
    height: 20px;
    margin-right: 15px;

    @include vp-767 {
      margin-right: 0;
    }
  }
}

.header__callback {
  margin-right: 60px;
  margin-top: 5px;
  align-self: flex-start;

  @include vp-1023 {
    display: none;
  }
}
